.form-content {
    background-color: #fefefe;

    /* centered form window */
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    border: 1px solid #bdbdbd;
    width: 80%; /* Could be more or less, depending on screen size */
    /* height: 400px; */
    font-family: Arial, Helvetica, sans-serif;
}

