.sub-condition-1 {
    display: inline-block;
    width: 100%;
    background: #eeeeee;
}

.para-main-select-field2 {
    float: left;
    padding: 10px 10px;
    margin-top: 20px;
    margin-left: 30px;
    margin-right: 10px;
    margin-bottom: 20px;
    font-size: 16px;
    border: 1px solid grey;
    border-radius: 4px;
}

.para-main-input-text2 {
    float: left;
    display: inline-block;
    padding: 10px 10px;
    margin-top: 20px;
    margin-right: auto;
    width: calc(100% - 600px);
    font-size: 16px;
    border: 1px solid grey;
    border-radius: 4px;
}

.para-main-input-text2::placeholder {
    font-style: italic;
}
